
.nav{
  &__list {
    margin: 0;
    padding: 0;
    list-style: none;

    display: flex;
    flex-direction: column;
    gap: 32px;

  }

  &__link {
    position: relative;
    color: $white-color;
    text-decoration: none;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;

    &::after {
      content: "";
      display: block;
      width: 100%;
      position: absolute;
      bottom: -8px;
      height: 1px;
      background-color: $white-color;

      transition: transform 0.3s;
      transform: scale(0);
      transform-origin:left;
    }

    &:hover::after {
      transform: scale(1);
    }
  }
}
