.top-bar {
  padding-block: 24px;
  @include contentPaddingInLine;
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include onTablet {
    padding-block: 50px;
  }

  &__icons {
    display: flex;
    gap: 24px;
  }

  &__logo-link {
    display: flex;
  }

  &__logo {
    @include hover(transform,scale(1.2));

  }
}


